/**
 * CallNativeService
 */
import { Injectable } from '@angular/core';


/**
 * 
 */
const TARGET_VIEW = 'mainView';

/**
 * URL
 */
export enum InAppBrowserTarget {
    /**
     *  URL Cordova WebView InAppBrowser 
     */
    Self = '_self',
    /**
     * InAppBrowser 
     */
    Blank = '_blank',
    /**
     *  Web  ( systems web browser ) 
     */
    System = '_system'
}

/**
 * IinAppBrowserArgs
 */
export interface IinAppBrowserArgs {
    /**
     * URL
     */
    url: string;
    /**
     * URL Web 
     */
    target?: InAppBrowserTarget;
}

/**
 * localNotificationArgs
 */
export interface IlocalNotificationArgs {
    /**
     * ID
     */
    id: number;
    /**
     * 
     */
    title: string;
    /**
     * 
     */
    text: string;
    /**
     * 
     */
    trigger?: {
        /**
         * ISO
         */
        at: string
    };
    /**
     * 
     */
    icon?: string;
    /**
     * 
     */
    smallIcon?: string;
    /**
     * 
     */
    foreground?: boolean;
}

export enum FidoAction {
    /**
     * 
     */
    Register = 'register',
    /**
     * 
     */
    Authentication = 'authentication',
    /**
     * 
     */
    Remove = 'remove',
    /**
     * 
     */
    RegisterList = 'registerList'
}

export interface IFidoArgs {
    action: FidoAction;
    user: string;
    handle?: string;
}

export interface IDeviceResult {
    cordova: string;
    model: string;
    platform: string;
    uuid: string;
    version: string;
    isVirtual: string;
    serial: string;
}

@Injectable({
    providedIn: 'root'
})
export class CallNativeService {
    private reserveData: string | null;

    constructor() {
        this.reserveData = null;
        window.addEventListener('message', (res) => {
            if (res.origin === location.origin) {
                return;
            }
            try {
                this.reserveData = JSON.parse(res.data);
            } catch (err) {
                console.error(err);
            }
        });
    }

    /**
     * 
     * @method postMessage
     * @param data {any}
     */
    private postMessage(data: any) {
        const json: string = JSON.stringify(data);
        (<any>window).wizViewMessenger.postMessage(json, TARGET_VIEW);
    }

    /**
     * 
     * @method reserveMessage
     */
    private reserveMessage() {
        return new Promise<any>((resolve) => {
            const time = 600;
            const timer = setInterval(() => {
                const data = this.reserveData;
                if (data !== null) {
                    resolve(data);
                    this.reserveData = null;
                    clearInterval(timer);
                }
            }, time);
        });
    }

    /**
     * FIDO
     * @method fido
     * @param args {IFidoArgs}
     */
    public async fido(args: IFidoArgs) {
        const data = {
            method: 'fido',
            option: args
        };
        let result;
        if ((<any>window).wizViewMessenger !== undefined) {
            this.postMessage(data);
            result = await this.reserveMessage();
            console.log(result);
        } else {
            result = {
                isSuccess: true,
                result: ['test']
            };
        }

        return result;
    }

    /**
     * device
     * @method device
     */
    public async device(): Promise<IDeviceResult | null> {
        const data = { method: 'device' };
        let result;
        if ((<any>window).wizViewMessenger !== undefined) {
            this.postMessage(data);
            result = await this.reserveMessage();
            console.log(result);
            return result;
        } else {
            const browser = 'browser';
            return {
                cordova: browser,
                model: browser,
                platform: browser,
                uuid: browser,
                version: browser,
                isVirtual: browser,
                serial: browser,
            };
        }
    }

    /**
     * inAppBrowser
     * @method postMessage
     * @param args {IinAppBrowserArgs}
     */
    public inAppBrowser(args: IinAppBrowserArgs) {
        const data = {
            method: 'inAppBrowser',
            option: args
        };
        if ((<any>window).wizViewMessenger !== undefined) {
            this.postMessage(data);
        }
    }

    /**
     * localNotification
     * @method localNotification
     * @param args {IlocalNotificationArgs}
     */
    public localNotification(args: IlocalNotificationArgs) {
        const data = {
            method: 'localNotification',
            option: args
        };
        if ((<any>window).wizViewMessenger !== undefined) {
            this.postMessage(data);
        }
    }
}
